import React from 'react';

import './css/app.scss'
import Main from './scripts/main';

function App() {
    var date = new Date();
    var options = { year: 'numeric', month: 'short', day: 'numeric' };

    return (
        <div className="App">
            <body>
                <header></header>
                <Main />
                <footer>Last update : {date.toLocaleDateString('en-US', options)} </footer>
            </body>
        </div>
    );
}

export default App;
